<template>
  <div class="detail-container">
    <!--  -->
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" @click-right="onClickRight" fixed>
      <template #title>
        <van-tabs v-model="active" @click="onTabClick">
          <van-tab title=""></van-tab>
          <van-tab title=""></van-tab>
          <van-tab title=""></van-tab>
          <van-tab title=""></van-tab>
        </van-tabs>
      </template>
      <template #right>
        <van-icon name="share-o" size="24" />
      </template>
    </van-nav-bar>

    <!--  -->
    <van-swipe :autoplay="2000" indicator-color="#f00">
      <van-swipe-item v-for="(item,index) in topImages" :key="index">
        <img :src="item" alt="" />
      </van-swipe-item>
    </van-swipe>

    <!--  -->
    <detail-base-info :goods="goods" id=""></detail-base-info>

    <!--  -->
    <detail-shop-info :shop="shop"></detail-shop-info>

    <!--  -->
    <detail-goods-info :detailInfo="detailInfo"></detail-goods-info>

    <!--   -->
    <van-cell id="" icon="comment-o">
      <template #title></template>
    </van-cell>
    <detail-comment-info :commentInfo="commentInfo" />

    <!--  -->
    <van-cell id="" icon="comment-o">
      <template #title></template>
    </van-cell>
    <detail-param-info :paramInfo="paramInfo"></detail-param-info>

    <!--  -->
    <van-cell id="" icon="comment-o">
      <template #title></template>
    </van-cell>
    <product-grid :product-list="recommends" />

    <!--   -->
    <back-top @click.native="backClick" v-show="isShowBackTop"></back-top>

    <!--  -->
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="" @click="onClickIcon" />
      <van-goods-action-icon icon="shop-o" text="" @click="onClickIcon" />
      <van-goods-action-icon icon="cart-o" text="" :badge="cartLength()" to="/cart" />
      <van-goods-action-icon icon="star" text="" color="#ff5000" @click="onClickIcon" />
      <van-goods-action-button type="warning" text="" @click="addToCart" />
      <van-goods-action-button type="danger" text="" @click="onClickIcon" />
    </van-goods-action>

    <!--  -->
    <van-share-sheet v-model="showShare" title="" :options="options" @select="onSelect" />

  </div>
</template>

<script>
import {
  getDetail,
  Goods,
  Shop,
  GoodsParam,
  getRecommend
} from '@/network/detail'
import { CartGoods } from '@/network/cart'

import DetailBaseInfo from './childComps/DetailBaseInfo.vue'
import DetailShopInfo from './childComps/DetailShopInfo.vue'
import DetailGoodsInfo from './childComps/DetailGoodsInfo.vue'
import DetailParamInfo from './childComps/DetailParamInfo.vue'
import DetailCommentInfo from './childComps/DetailCommentInfo.vue'
import ProductGrid from '@/components/productGrid/ProductGrid.vue'
import BackTop from '@/components/backTop/BackTop.vue'

import { Toast } from 'vant'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'Detail',
  components: {
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    DetailParamInfo,
    DetailCommentInfo,
    ProductGrid,
    BackTop
  },
  data () {
    return {
      active: 0,
      iid: null,
      topImages: [],
      goods: {},
      shop: {},
      detailInfo: {},
      paramInfo: {},
      commentInfo: {},
      recommends: [],
      isShowBackTop: false,
      showShare: false,
      options: [
        [
          { name: '', icon: 'wechat' },
          { name: '', icon: 'wechat-moments' },
          { name: 'QQ', icon: 'qq' },
          { name: '', icon: 'weibo' }
        ],
        [
          { name: '', icon: 'link' },
          { name: '', icon: 'poster' },
          { name: '', icon: 'qrcode' },
          { name: '', icon: 'weapp-qrcode' }
        ]
      ]
    }
  },
  created () {
    // iid
    this.iid = this.$route.params.iid
    // 
    getDetail(this.iid).then((res) => {
      console.log(res)
      const data = res.result
      // 
      this.topImages = data.itemInfo.topImages
      // console.log(this.topImages)
      // 
      this.goods = new Goods(
        data.itemInfo,
        data.columns,
        data.shopInfo.services
      )

      // 
      this.shop = new Shop(data.shopInfo)

      // 
      this.detailInfo = data.detailInfo

      // detailParam
      this.paramInfo = new GoodsParam(
        data.itemParams.info,
        data.itemParams.rule
      )

      // 
      if (data.rate.cRate !== 0) {
        this.commentInfo = data.rate.list[0]
      }
      this.itemInfo = data.itemInfo
    })

    // 
    getRecommend().then((res) => {
      console.log(res)
      res.data.list.forEach(recommendItem => {
        // recommendItem.iid = recommendItem.item_id
        recommendItem.show = {}
        recommendItem.show.img = recommendItem.image
      })
      this.recommends = res.data.list
      console.log(this.recommends)
    })
  },
  mounted () {
    window.addEventListener('scroll', this.scrollToTop)
  },
  destroyed () {
    window.removeEventListener('scroll', this.scrollToTop)
  },
  methods: {
    ...mapActions(['addCart']),
    ...mapGetters(['cartLength']),

    onClickLeft () {
      this.$router.back()
    },
    onClickRight () {
      this.showShare = true
    },
    onTabClick (name, title) {
      console.log(name, title)
      // const currentScroll = Math.ceil(document.documentElement.scrollTop)
      // this.active = name
      // console.log(document.querySelector('#').offsetTop - 46)
      switch (title) {
        case '':
          document.documentElement.scrollTop = 0
          document.body.scrollTop = 0
          break
        case '':
          document.documentElement.scrollTop = document.querySelector('#').offsetTop - 46
          document.body.scrollTop = document.querySelector('#').offsetTop - 46
          break
        case '':
          document.documentElement.scrollTop = document.querySelector('#').offsetTop - 46
          document.body.scrollTop = document.querySelector('#').offsetTop - 46
          break
        case '':
          document.documentElement.scrollTop = document.querySelector('#').offsetTop - 46
          document.body.scrollTop = document.querySelector('#').offsetTop - 46
          break
      }
    },
    backClick () {
      // 
      let top = document.documentElement.scrollTop || document.body.scrollTop
      console.log(top)
      if (top >= 700) {
        this.isShowBackTop = true
      }
      // 
      const timeTop = setInterval(() => {
        document.body.scrollTop = document.documentElement.scrollTop = top -= 500
        if (top <= 0) {
          clearInterval(timeTop)
        }
      }, 10)
    },
    scrollToTop () {
      const top = document.documentElement.scrollTop || document.body.scrollTop
      // console.log(2)
      if (top >= 700) {
        this.isShowBackTop = true
      } else {
        this.isShowBackTop = false
      }
      // console.log(top)
      // console.log(document.querySelector('#').offsetTop - 46)
      // console.log(document.querySelector('#').offsetTop - 46)
      // console.log(document.querySelector('#').offsetTop - 46)
      // console.log(document.querySelector('#').offsetTop - 46)

      if (top < document.querySelector('#').offsetTop - 48) {
        this.active = 0
      } else if (top < document.querySelector('#').offsetTop - 48) {
        this.active = 1
      } else if (top < document.querySelector('#').offsetTop - 48) {
        this.active = 2
      } else {
        this.active = 3
      }
    },
    onSelect (option) {
      Toast(option.name)
      if (option.icon === 'link') {
        Toast.success('')
      }
      this.showShare = false
    },
    onClickIcon () {
      Toast('')
    },
    addToCart () {
      const product = new CartGoods(this.itemInfo)
      // this.$store.dispatch("addCart", product);
      console.log(product)
      // mapActionsstore
      //  toasttoastCart
      this.addCart(product).then((res) => {
        Toast(res)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.detail-container {
  padding: 46px 0 50px 0;
}
.van-swipe {
  height: 300px;
  overflow: hidden;
  text-align: center;
}

.van-swipe-item img {
  width: 100%;
}
/deep/.van-tab {
  margin: 0 6px;
}
</style>
